import CoinStore from '../../domains/stores/CoinStore';
import CustomElement from '../../abstracts/CustomElement';
import { $ } from '../../utils';

class CoinCurrentSituation extends CustomElement {
  connectedCallback() {
    super.connectedCallback();
    CoinStore.instance.subscribeMachine(this);
  }

  // eslint-disable-next-line max-lines-per-function
  template() {
    return `
      <h2>  </h2>
      <table class="coin-current-situation">
        <thead>
          <tr>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>500</td>
            <td class="coin-current-situation__coin-500-count-td">0</td>
          </tr>
          <tr>
            <td>100</td>
            <td class="coin-current-situation__coin-100-count-td">0</td>
          </tr>
          </tr>
          <tr>
            <td>50</td>
            <td class="coin-current-situation__coin-50-count-td">0</td>
          </tr>
          <tr>
            <td>10</td>
            <td class="coin-current-situation__coin-10-count-td">0</td>
          </tr>
        </tbody>
      </table>
    `;
  }

  rerender({ coinsCount }) {
    $('.coin-current-situation__coin-500-count-td').textContent = `${coinsCount[500]}`;
    $('.coin-current-situation__coin-100-count-td').textContent = `${coinsCount[100]}`;
    $('.coin-current-situation__coin-50-count-td').textContent = `${coinsCount[50]}`;
    $('.coin-current-situation__coin-10-count-td').textContent = `${coinsCount[10]}`;
  }
}

customElements.define('coin-current-situation', CoinCurrentSituation);

export default CoinCurrentSituation;
